* { margin: 0; padding: 0; }

body, html {
  height: 100%;
  max-width: 600px;
  font-family: sans-serif;
  font-size: 0px;
}

#app {
  position: relative;
  max-width: 100%;
  max-height: 100%;
}

button {
  width: 50%;
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 4px;
  border: 4px solid;
  background: -webkit-gradient(linear, left top, left bottom, from(#EAF201), to(#E45B10));
  background: -webkit-linear-gradient(top, #EAF201, #E45B10);
  background: -o-linear-gradient(top, #EAF201, #E45B10);
  background: linear-gradient(to bottom, #EAF201, #E45B10);
  font-size: 16px;
  font-weight: bold;
}

button:focus {
  outline: none;
}



